import '../css/resources.scss';

import {createSearchContext} from 'upi-fuzzy-search';
import ServiceProvider from './modules/core/ServiceProvider';

const svc = new ServiceProvider( {
    baseURL: `/${upi.env.COUNTRY_CODE}/resources/international-resources/_vti_bin/Distributors.svc/Distributors/`,
    cache: true,
    responseType: 'text',
    headers: {
        'Content-Type': 'text/html'
    }
} );

$( document ).ready( function () {

    var menus = $( '#page-content .dropDownSelect' );
    var sdsFilter = $( '#sdsFilter' );
    var sdsList = $( '#sds-resource-list' );

    //menus.find('a').gotoByScroll('#intlResourceData');

    menus = menus.dropDownSelect();

    $( '#intlCountrySelect .menu' ).on( 'click', 'a', function ( e ) {
        if ( !e.target.href ) {
            return;
        }

        $( document ).trigger( 'menu:close' );

        var href = e.target.href,
            cid = href.substr( href.lastIndexOf( 'cid=' ) + 4 ),
            resource_div = $( '#intlResourceData' ),
            loading_id = null;

        function display_data ( data ) {
            resource_div.hide().empty().html( data ).fadeIn();
        }

        function loading ( is_loading ) {
            // clear any previous loading timers
            if ( loading_id ) {
                clearTimeout( loading_id );
                loading_id = null;
            }

            if ( is_loading ) {
                loading_id = setTimeout( function () {
                    display_data( '<div><i class="loading small"></i>Loading...</div>' );
                }, 500 );
            }
        }

        loading( true );
        svc.get( cid )
            .then( res => display_data( res.data ) )
            .catch( error => {
                display_data( `
                    <div class="module paddedBlock error">
                        <span class="msg">There was an error while attempting to retrieve this information</span>
                    </div>
                ` );
            } )
            .then( () => {
                loading( false );
                menus.hideMenu();
            } );

        e.preventDefault();
        return false;
    } )

    // if sdsFilter present then create an indexed list of the resource list
    if ( sdsFilter ) {

        const resourceIndex = sdsList.find( 'li' ).map( ( i, item ) => {
            const $item = $( item );
            return {
                title: $item.text().trim(),
                href: $item.find( 'a' ).attr( 'href' ),
                node: $item
            };
        } );

        const
            sdsContext = createSearchContext( resourceIndex, ['title'] ),
            renderer = createSDSRenderer( sdsContext );

        // update list based on defualt input value
        if ( sdsFilter.val() ) {
            renderer( sdsFilter.val() );
        }

        sdsFilter.on( 'input', e => {
            renderer( e.target.value );
        } )
    }

    /**
     * HOC that returns a functoin that accepts a query to be executed in a given
     * search context provided from upi-fuzzy-search
     * @param  {upi-fuzzy-search} srchContext search context to execute query from
     * @return {Function(String)} HOC to execute query
     */
    function createSDSRenderer ( srchContext ) {
        return query => {
            const results = srchContext.query( query.trim() );
            sdsList.html( buildSDSList( results ) );
        }
    }

    function buildSDSList ( items ) {
        return items.map( item => {
            return item.node;
        } )
    }
} );



